.search-bar {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
}

.search-bar input {
  border: 3px solid silver;
  height: 3rem;
  width: 50vw;
  font-size: 1.8rem;
  font-weight: 700;
  padding: 1.2rem;
}

input::placeholder {
  color: #425664;
}
