.image-list {
  display: grid;
  justify-items: center;
  padding: 20px 60px;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  grid-auto-rows: 10px;
}

.image-list img {
  width: 250px;
}

@media screen and (max-width: 800px) {
  .image-list {
    padding: 10px;
  }
}
